import React, { useEffect, useState } from "react";
import PageLayout from "../../layout";
import {
  PostCard,
  RandomCard,
  Slider as Slide,
  CategoryCard,
  LatestCard,
} from "../../containers";
import { hooks } from "../../hooks";
import Slider from "react-slick";
import Marque from "../../containers/Marque";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styles from "./styles.module.css";
import {
  FaFacebook,
  FaInstagram,
  FaLinkedinIn,
  FaTwitter,
  FaYoutube,
} from "react-icons/fa";
import { Link } from "react-router-dom";
import { Tweet } from "react-tweet";
const Home = () => {
  const posts = hooks.usePost();
  const categories = hooks.useCategory();

  function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }

  const [varRandom, setVarRandom] = React.useState([]);

  React.useEffect(() => {
    function getRandomItems(arr, numItems) {
      const randomItems = [];
      const totalItems = arr.length;

      while (randomItems.length < numItems) {
        const randomIndex = getRandomInt(0, totalItems);

        if (!randomItems.includes(randomIndex)) {
          randomItems.push(randomIndex);
        }
      }
      const selectedItems = randomItems.map((index) => arr[index]);

      return selectedItems;
    }

    if (posts.length > 5) {
      const randomNewsItems = getRandomItems(posts, 3);
      setVarRandom(randomNewsItems);
    }
  }, [posts]);

  const reversePost = [...posts]?.reverse();

  const [slidesToShow, setSlidesToShow] = useState(3);

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 767) {
        setSlidesToShow(1); // 1 slide on mobile
      } else {
        setSlidesToShow(2); // 4 slides on desktop
      }
    };

    // Initial setup
    handleResize();

    // Add event listener for window resize
    window.addEventListener("resize", handleResize);

    // Clean up the event listener
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const settings = {
    dots: true,
    infinite: true,
    slidesToShow: slidesToShow,
    slidesToScroll: 1,
    autoplay: true,
    speed: 10000,
    autoplaySpeed: 2000,
    cssEase: "linear",
  };

  const alerts = reversePost?.filter((x) => x.type === "alert");
  const headline = reversePost?.filter((x) => x.type === "headline");
  const featured = reversePost?.filter((x) => x.type === "featured");

  const [postLimit, setPostLimit] = useState(4);

  const loadMore = () => {
    setPostLimit(postLimit + 2);
  };

  return (
    <PageLayout>
      <Slide posts={featured} />
      <div></div>
      <Slider {...settings}>
        {alerts?.map((item, i) => (
          <Marque post={item} key={i} />
        ))}
      </Slider>
      <br />
      <br />
      <div className={styles.homeContainer}>
        <div className={styles.leftContainer}>
          <div className={styles.latestContents}>
            <div className={styles.latest}>
              {reversePost
                ?.map((item, i) => <LatestCard post={item} key={i} />)
                .slice(0, 1)}
              <div className={styles.random}>
                <h5>Headline</h5>
                <div className={styles.randomItem}>
                  {headline
                    ?.map((item, i) => (
                      <div className={styles.rdItem} key={i}>
                        <h6>{item.title}</h6>
                      </div>
                    ))
                    .slice(0, 5)}
                </div>
              </div>
            </div>
          </div>
          <div className={styles.postFlex}>
            {posts
              ?.map((item, i) => <PostCard post={item} key={i} />)
              .slice(0, postLimit)}
          </div>
          <br />
          <br />
          {postLimit !== posts?.length && (
            <div className="btnContainer">
              <button type="button" className="btn btnBlack" onClick={loadMore}>
                Load More
              </button>
            </div>
          )}
        </div>
        <div className={styles.rightContainer}>
          <div className="btnContainer right">
            <Link
              to="/posts"
              style={{
                textDecoration: "none",
                fontSize: "11px",
                color: "black",
              }}
            >
              View All Posts
            </Link>
            {varRandom?.map((item, i) => (
              <RandomCard post={item} key={i} />
            ))}
          </div>
          {/* social Media */}
          <div className={styles.socialMedia}>
            <h6>Join Us</h6>
            <a
              href="https://web.facebook.com/lotusbetaanalytics"
              className={`${styles.SocialMDButton} ${styles.facebook}`}
            >
              <FaFacebook />
              &nbsp;Facebook
            </a>
            <a
              href="https://twitter.com/lotusbeta"
              className={`${styles.SocialMDButton} ${styles.twitter}`}
            >
              <FaTwitter />
              &nbsp;X
            </a>
            <a
              href="https://www.instagram.com/lbannigeria"
              className={`${styles.SocialMDButton} ${styles.insta}`}
            >
              <FaInstagram />
              &nbsp;Instagram
            </a>
            <a
              href="https://www.linkedin.com/company/lotus-beta-analytics"
              className={`${styles.SocialMDButton} ${styles.linked}`}
            >
              <FaLinkedinIn />
              &nbsp;LinkedIn
            </a>
            <a
              href="https://www.youtube.com/channel/UCOhLIAisZyiolylqVhuq2qg"
              className={`${styles.SocialMDButton} ${styles.youtube}`}
            >
              <FaYoutube />
              &nbsp;Youtube
            </a>
          </div>
          {/* end of social media */}
          {/* categories */}
          <div className={styles.categories}>
            <h6>Categories</h6>
            <div className={styles.categoriesFlex}>
              {categories
                ?.map((item, i) => <CategoryCard category={item} key={i} />)
                .slice(0, 3)}
            </div>
          </div>
          {/* categories */}
          <div data-theme="light">
            <Tweet id="1722154491783602599" />
          </div>
        </div>
      </div>
      <br />
      <br />
    </PageLayout>
  );
};

export default Home;
